<template>
    <v-container>
        <v-card color="blue" v-if="entregaSeleccionada != null" class="my-3">
            <v-row><v-col align="center" ><div class="itemUnaEntrega"># {{entregaSeleccionada.OrdenEntrega}}</div></v-col></v-row>
            <v-row><v-col align="center" ><div class="itemUnaEntrega">{{entregaSeleccionada.Comprobante}}</div></v-col></v-row>
            <v-row><v-col align="center" ><div class="itemUnaEntrega">{{entregaSeleccionada.NombreCliente}}</div></v-col></v-row>
            <v-row><v-col align="center" ><div class="itemUnaEntrega">{{entregaSeleccionada.NombreDestino}}</div></v-col></v-row>
            <v-row><v-col align="center" ><div class="itemUnaEntrega">{{entregaSeleccionada.Domicilio}}</div></v-col></v-row>
            <v-row><v-col align="center" ><div class="itemUnaEntrega">{{entregaSeleccionada.Localidad}}</div></v-col></v-row>
            <v-row><v-col align="center" ><div class="itemUnaEntrega">Bultos: {{entregaSeleccionada.Bultos}}</div></v-col></v-row>
            <v-row><v-col><v-btn @click="clickEnOK()" color="green" block dark><div class="itemUnaEntrega">OK</div></v-btn></v-col></v-row>
            <v-row><v-col><v-btn @click="clickEnNo()" color="red" block dark><div class="itemUnaEntrega">NO</div></v-btn></v-col></v-row>
            <v-row><v-col><v-btn @click="clickEnParcial()" color="orange" block dark><div class="itemUnaEntrega">PARCIAL</div></v-btn></v-col></v-row>
            <v-row><v-col><v-btn @click="clickEnCancelar()" color="blue" block dark><div class="itemUnaEntrega">CANCELAR</div></v-btn></v-col></v-row>
        </v-card>
        <v-alert @click="clickEnEntrega(item)" :color="getColorEntrega(item)" v-for="(item, index) in listaEntregas" class="my-1" v-if="entregaSeleccionada==null">
            <v-row class="py-0 my-0">
                <v-col align="center" class="py-0 my-0">
                    <div class="itemEntrega">{{item.NombreCliente}}</div>
                </v-col>
            </v-row>
            <v-row class="py-0 my-0">
                <v-col align="center" class="py-0 my-0">
                    <div class="itemEntrega">{{item.Comprobante}}</div>
                </v-col>
            </v-row>
            <v-row class="py-0 my-0">
                <v-col align="center" class="py-0 my-0">
                    <div class="itemEntrega">{{item.NombreDestino}}</div>
                </v-col>
            </v-row>
        </v-alert>
    </v-container>
</template>

<style scoped>
    .itemUnaEntrega {
        color: white;
        font-size: 16px;
        font-weight: 900;
        font-family: "Helvetica Neue";
    }
    .itemEntrega {
        color: white;
        font-size: 16px;
        font-weight: 900;
        font-family: "Helvetica Neue";
    }
</style>

<script>
import choferes from "@/store/choferes"
import store from "@/store"

export default {
    name: "VerRutaActual",

    data() {
        return {
            listaEntregas: [],
            entregaSeleccionada: null,
        }
    },

    methods: {
        getColorEntrega(item) {
            if (item.Estado=="ENTREGADO") {
                return "green"
            } else if (item.Estado=="NO ENTREGADO") {
                return "red"
            } else if (item.Estado=="PARCIAL") {
                return "orange"
            } else {  
                return "blue"
            }
        },
        clickEnCancelar() {
            this.entregaSeleccionada=null
        },
        clickEnOK() {
            const ubicacionEntrega=this.listaEntregas.indexOf(this.entregaSeleccionada)
            this.listaEntregas[ubicacionEntrega].Estado="ENTREGADO"
            this.entregaSeleccionada=null
        },
        clickEnNo() {
            const ubicacionEntrega=this.listaEntregas.indexOf(this.entregaSeleccionada)
            this.listaEntregas[ubicacionEntrega].Estado="NO ENTREGADO"
            this.entregaSeleccionada=null
        },
        clickEnParcial() {
            const ubicacionEntrega=this.listaEntregas.indexOf(this.entregaSeleccionada)
            this.listaEntregas[ubicacionEntrega].Estado="PARCIAL"
            this.entregaSeleccionada=null
        },
        clickEnEntrega(item) {
            this.entregaSeleccionada=item
        },
        getEntregas() {            
            choferes.actions.getEntregas(store.state.choferes.choferActual.numero, "2021-04-01", 1) 
                .then(respuesta => {
                   this.listaEntregas=respuesta
                })
                .catch(puteada => {
                    console.log(puteada)
                })
        },
    },

    created() {
        this.getEntregas()
    }
}
</script>